import { css, html, LitElement } from 'lit';
import { deeplink, pushState } from './deeplink';

import './global.css.js';
import { MERCH_CARD_NODE_NAME } from './merch-card';

const MERCH_CARDS = 'merch-cards';

const SortOrder = {
    alphabetical: 'alphabetical',
    authored: 0,
};

const makeCategoryFilter =
    ({ filter }) =>
    (elements) =>
        elements.filter((element) => element.filters.hasOwnProperty(filter));

const makeTypeFilter = ({ types }) => {
    if (!types) {
        return (elements) => elements;
    }
    types = types.split(',');
    return (elements) =>
        elements.filter((element) =>
            types.some((type) => element.types.includes(type))
        );
};

const makeAlphabeticalSorter = () => (elements) =>
    elements.sort((a, b) =>
        (a.title ?? '').localeCompare(b.title ?? '', 'en', {
            sensitivity: 'base',
        })
    );

const makeAuthoredSorter =
    ({ filter }) =>
    (elements) =>
        elements.sort((a, b) => {
            if (a.filters[filter] && b.filters[filter]) {
                return a.filters[filter] - b.filters[filter];
            }
            // if no explicit order, do not sort, keep same order.
            return -1;
        });

const makeSearcher = ({ search }) => {
    if (search?.length) {
        search = search.toLowerCase();
        return (elements) =>
            elements.filter((element) => element.includes(search));
    }
    return (elements) => elements;
};

export class MerchCards extends LitElement {
    static properties = {
        filter: { type: String, attribute: 'filter', reflect: true },
        filtered: { type: String, attribute: 'filtered' }, // freeze filter
        search: { type: String, attribute: 'search', reflect: true },
        sort: {
            type: Number,
            attribute: 'sort',
            default: SortOrder.authored,
            reflect: true,
        },
        types: { type: String, attribute: 'types', reflect: true },
        limit: { type: Number, attribute: 'limit' },
        page: { type: Number, attribute: 'page', reflect: true },
        singleApp: { type: String, attribute: 'single_app' },
        showMoreText: { type: String, attribute: 'show-more-text' },
        hasMore: { type: Boolean },
    };

    constructor() {
        super();
        // set defaults
        this.filter = 'all';
        this.hasMore = false;
    }

    render() {
        return html`<slot></slot> ${this.showMoreButton}`;
    }

    updated(changedProperties) {
        let updateChildren = false;
        const children = [...this.children].filter(
            (child) => child.tagName === MERCH_CARD_NODE_NAME
        );
        if (changedProperties.has('singleApp') && this.singleApp) {
            children.forEach((card) => {
                card.updateFilters(card.name === this.singleApp);
            });
        }
        if (
            !this._filters ||
            changedProperties.has('filter') ||
            changedProperties.has('types')
        ) {
            this._filters = [makeCategoryFilter(this), makeTypeFilter(this)];
            updateChildren = true;
        }
        if (!this._searcher || changedProperties.has('search')) {
            this._searcher = makeSearcher(this);
            updateChildren = true;
        }
        if (!this._sorter || changedProperties.has('sort')) {
            this._sorter =
                this.sort === SortOrder.alphabetical
                    ? makeAlphabeticalSorter(this)
                    : makeAuthoredSorter(this);
            updateChildren = true;
        }

        const reducers = [...this._filters, this._searcher, this._sorter];

        let result = reducers
            .reduce((elements, reducer) => reducer(elements), children)
            .map((element, index) => [element, index]);

        if (this.page && this.limit) {
            const pageSize = this.page * this.limit;
            this.hasMore = result.length > pageSize;
            result = result.filter(([, index]) => index < pageSize);
        }
        if (result.length > 0) {
            this.cardToScrollTo = result[result.length - 1][0];
        }
        let reduced = new Map(result);
        children.forEach((child) => {
            if (reduced.has(child)) {
                child.style.order = reduced.get(child);
                child.style.removeProperty('display');
            } else {
                child.style.display = 'none';
                child.style.removeProperty('order');
            }
        });
    }

    connectedCallback() {
        super.connectedCallback();
        this.startDeeplink();
        this.updateComplete.then(() => {
            if (this.filtered) {
                this.filter = this.filtered;
            }
            this.prepareShowMore();
        });
    }

    disconnectedCallback() {
        super.disconnectedCallback();
        this.stopDeeplink();
    }

    get showMoreButton() {
        if (!this.hasMore) return;
        return html`<button style="order: 1000;" @click="${this.showMore}">
            ${this.showMoreText}
        </button>`;
    }

    showMore() {
        pushState({ page: this.page + 1 });
        setTimeout(() => {
            this.scrollToShowMore();
        }, 1);
    }

    prepareShowMore() {
        if (this.page) {
            this.limit = this.limit || 27;
        } else {
            this.page = 1;
        }
    }

    scrollToShowMore() {
        this.cardToScrollTo.scrollIntoView({ behavior: 'smooth' });
    }

    startDeeplink() {
        this.stopDeeplink = deeplink(
            ({ filter, types, sort, search, single_app, page }) => {
                if (filter && filter !== this.filter) {
                    setTimeout(() => {
                        pushState({ page: undefined });
                        this.page = 1;
                    }, 1);
                }
                if (!this.filtered) {
                    this.filter = filter ?? this.filter;
                }
                this.types = types ?? '';
                this.search = search ?? '';
                this.singleApp = single_app;
                this.sort = sort;
                this.page = Number(page) || this.page;
            }
        );
    }

    static styles = [
        css`
            :host > button {
                grid-column: 1 / -1;
                place-self: baseline;
                background-color: transparent;
                border-radius: 16px;
                border: 2px solid var(--text-color, #2c2c2c);
                color: var(--text-color, #2c2c2c);
                display: inline-block;
                font-size: 15px;
                font-style: normal;
                font-weight: 700;
                line-height: 16px;
                padding: 5px 14px;
            }

            :host > button:hover {
                background-color: var(--color-black, #000);
                border-color: var(--color-black, #000);
                color: var(--color-white, #fff);
            }
        `,
    ];
}

MerchCards.SortOrder = SortOrder;

if (!customElements.get(MERCH_CARDS)) {
    customElements.define(MERCH_CARDS, MerchCards);
}
