// List of focusable element selectors
export const FOCUSABLE_SELECTORS = [
    'a[href]:not([disabled])',
    'button:not([disabled])',
    'textarea:not([disabled])',
    'input[type="text"]:not([disabled])',
    'input[type="radio"]:not([disabled])',
    'input[type="checkbox"]:not([disabled])',
    'select:not([disabled])',
    '[tabindex]:not([tabindex="-1"]):not([disabled])',
].join(', ');

export const [ARROW_LEFT, ARROW_RIGHT, ARROW_UP, ARROW_DOWN] = [
    'ArrowLeft',
    'ArrowRight',
    'ArrowUp',
    'ArrowDown',
];
export const FOCUS_KEYS = [
    'Tab',
    ARROW_LEFT,
    ARROW_RIGHT,
    ARROW_UP,
    ARROW_DOWN,
];

export function getFirstLastFocusableElement(container) {
    // Query all focusable elements in the container
    const focusableElements = container.querySelectorAll(FOCUSABLE_SELECTORS);

    // Return the first and last element in the list or empty array if there's no focusable element
    return focusableElements.length > 0
        ? [
              focusableElements[0],
              focusableElements[focusableElements.length - 1],
          ]
        : [];
}
