import { html, LitElement } from 'lit';
import { styles } from './merch-card.css.js';
import { encodeSvg } from './utils.js';
import secureTransactionIcon from './img/secure-transaction.svg';
import ellipsis from './img/ellipsis.svg';
import checkmark from './img/checkmark-white-small.svg';

import { ARROW_DOWN, ARROW_LEFT, ARROW_RIGHT, ARROW_UP } from './focus.js';
import './global.css.js';

export const MERCH_CARD_NODE_NAME = 'MERCH-CARD';
export const MERCH_CARD = 'merch-card';

export class MerchCard extends LitElement {
    static properties = {
        name: { type: String },
        variant: { type: String },
        type: { type: String },
        badgeColor: { type: String, attribute: 'badge-color' },
        badgeBackgroundColor: {
            type: String,
            attribute: 'badge-background-color',
        },
        badgeText: { type: String, attribute: 'badge-text' },
        icons: { type: Array },
        actionmenu: { type: Boolean, attribute: 'action-menu' },
        actionMenuContent: { type: String, attribute: 'action-menu-content' },
        title: { type: String },
        description: { type: String },
        image: { type: String, attribute: 'image' },
        customHr: { type: String, attribute: 'hr' },
        detailBg: { type: String, attribute: 'detail-bg' },
        secureLabel: { type: String, attribute: 'secure-label' },
        checkboxLabel: { type: String, attribute: 'checkbox-label' },
        evergreen: { type: Boolean },
        filters: {
            type: String,
            reflect: true,
            converter: {
                fromAttribute: (value) => {
                    return Object.fromEntries(
                        value.split(',').map((filter) => {
                            const [key, order] = filter.split(':');
                            const value = Number(order);
                            return [key, isNaN(value) ? {} : value];
                        })
                    );
                },
                toAttribute: (value) => {
                    return Object.entries(value)
                        .map(([key, value]) =>
                            isNaN(value) ? key : `${key}:${value}`
                        )
                        .join(',');
                },
            },
        },
        types: {
            type: String,
            attribute: 'types',
            reflect: true,
        },
    };

    static styles = [styles];

    constructor() {
        super();
        this.filters = {};
        this.types = '';
    }

    updated(changedProperties) {
        if (changedProperties.has('badgeBackgroundColor')) {
            this.style.border = `1px solid ${this.badgeBackgroundColor}`;
        }
    }

    renderIcons() {
        return this.icons && this.icons.length > 0
            ? html`
                  <div class="icons">
                      ${this.icons.map(
                          (icon) =>
                              html`<img src="${icon.src}" alt="${icon.alt}" />`
                      )}
                  </div>
              `
            : '';
    }

    createCheckBox() {
        this.style.setProperty(
            '--checkmark-image',
            `url(${encodeSvg(checkmark)})`
        );
        return this.checkboxLabel
            ? html`
                  <div class="checkbox-container">
                      <input id="alt-cta" type="checkbox" />
                      <span
                          class="checkmark"
                          @click="${this.toggleCheckBox}"
                      ></span>
                      <label class="checkbox-label"
                          >${this.checkboxLabel}</label
                      >
                  </div>
              `
            : '';
    }

    createPlansFooter() {
        const footerSlot = html` <slot name="footer"></slot>`;
        const secureLabel = this['secureLabel'];
        return secureLabel
            ? html` <div class="standard-wrapper">
                  <div class="secure-transaction-wrapper">
                      <span
                          class="secure-transaction-icon"
                          style="background-image: url('${encodeSvg(
                              secureTransactionIcon
                          )}')"
                      ></span>

                      <span class="secure-transaction-label"
                          >${secureLabel}</span
                      >
                  </div>
                  ${footerSlot}
              </div>`
            : footerSlot;
    }

    decorateRibbon() {
        let additionalStyles;
        if (!this.badgeBackgroundColor || !this.badgeColor || !this.badgeText) {
            return;
        }
        if (this.evergreen) {
            additionalStyles = `border: 1px solid ${this.badgeBackgroundColor}; border-right: none;`;
        }
        return html`
            <div
                class="${this.variant}-ribbon"
                style="background-color: ${this
                    .badgeBackgroundColor}; color: ${this
                    .badgeColor}; ${additionalStyles
                    ? ` ${additionalStyles}`
                    : ''}"
            >
                ${this.badgeText}
            </div>
        `;
    }

    toggleCheckBox() {
        const checkbox = this.shadowRoot.querySelector('#alt-cta');
        checkbox.checked = !checkbox.checked;
        const footerSlot = this.shadowRoot.querySelector('slot[name="footer"]');
        if (footerSlot) {
            const nodes = footerSlot.assignedNodes({ flatten: true });
            let active, altCta;
            nodes.forEach((node) => {
                altCta = node.querySelector('.alt-cta');
                active = node.querySelector('.active');
            });
            if (active && altCta) {
                active.classList.toggle('button--inactive', checkbox.checked);
                altCta.classList.toggle('button--inactive', !checkbox.checked);
            }
        }
    }

    toggleActionMenu() {
        const actionMenuContentSlot = this.shadowRoot.querySelector(
            'slot[name="action-menu-content"]'
        );
        actionMenuContentSlot.classList.toggle('hidden');
    }

    get title() {
        const heading = this.querySelector('[slot="heading-xs"]');
        return heading.textContent.trim();
    }

    /**
     * If the card is the single app, set the order for all filters to 2.
     * If not, increment the order for all filters after the second card by 1.
     * @param {*} singleApp
     */
    updateFilters(singleApp) {
        const newFilters = { ...this.filters };
        Object.keys(newFilters).forEach((key) => {
            // if single app set the order for all filters to 2.
            if (singleApp) {
                newFilters[key] = Math.min(newFilters[key], 2);
                return;
            }
            const value = newFilters[key];
            // skip first card and cards without explicit order
            if (value === 1 || isNaN(value)) return;
            newFilters[key] = Number(value) + 1;
        });
        this.filters = newFilters;
    }

    includes(text) {
        return this.textContent.match(new RegExp(text, 'i')) !== null;
    }

    render() {
        switch (this.variant) {
            case 'special-offers':
                return this.renderSpecialOffer();
            case 'segment':
                return this.renderSegment();
            case 'plans':
                return this.renderPlans();
            case 'catalog':
                return this.renderCatalog();
            default:
                return html` <div />`;
        }
    }

    renderSpecialOffer() {
        return html` <div
                class="image"
                style="${this.image
                    ? `background-image: url(${this.image})`
                    : ''}"
            >
                ${this.decorateRibbon()}
            </div>
            <div class="body">
                <slot name="detail-m"></slot>
                <slot name="heading-xs"></slot>
                <slot name="body-xs"></slot>
            </div>
            ${this.evergreen
                ? html`
                      <div
                          class="detail-bg-container"
                          style="background: ${this['detailBg']}"
                      >
                          <slot name="detail-bg"></slot>
                      </div>
                  `
                : html`
                      <hr />
                      <slot name="footer"></slot>
                  `}`;
    }

    renderSegment() {
        return html` ${this.decorateRibbon()}
            <div class="body">
                <slot name="heading-xs"></slot>
                <slot name="body-xs"></slot>
            </div>
            <hr />
            <slot name="footer"></slot>`;
    }

    renderPlans() {
        return html` ${this.decorateRibbon()}
            <div class="body">
                ${this.renderIcons()}
                <slot name="heading-xs"></slot>
                <slot name="heading-m"></slot>
                <slot name="body-xxs"></slot>
                <slot name="body-xs"></slot>
                ${this.createCheckBox()}
            </div>
            ${this.createPlansFooter()}`;
    }

    renderCatalog() {
        return html` <div class="body">
                <div class="top-section">
                    ${this.renderIcons()} ${this.decorateRibbon()}
                    <div
                        class="action-menu ${!this.actionmenu
                            ? 'hidden'
                            : 'invisible'}"
                        style="background-image: url(${encodeSvg(ellipsis)})"
                        @click="${this.toggleActionMenu}"
                    ></div>
                </div>
                <slot
                    name="action-menu-content"
                    class="action-menu-content ${!this.actionMenuContent
                        ? 'hidden'
                        : ''}"
                    >${this.actionMenuContent}</slot
                >
                <slot name="heading-xs"></slot>
                <slot name="heading-m"></slot>
                <slot name="body-xxs"></slot>
                <slot name="body-xs"></slot>
            </div>
            <slot name="footer"></slot>`;
    }

    connectedCallback() {
        super.connectedCallback();
        this.setAttribute('tabindex', '0');
        this.addEventListener('keydown', this.keydownHandler);
    }

    disconnectedCallback() {
        super.disconnectedCallback();
        this.removeEventListener('keydown', this.keydownHandler);
    }

    // custom methods
    keydownHandler(e) {
        const currentCard = document.activeElement;
        if (currentCard && currentCard.tagName === MERCH_CARD_NODE_NAME) {
            let nextCard;

            const visibleCards = Array.from(
                this.parentElement.querySelectorAll('merch-card')
            )
                .filter(
                    (card) => window.getComputedStyle(card).display !== 'none'
                )
                .sort((a, b) => {
                    return (
                        parseInt(window.getComputedStyle(a).order, 0) -
                        parseInt(window.getComputedStyle(b).order, 0)
                    );
                });

            // Determine the number of columns by checking the position of cards
            let cols = 1;
            const firstCardTop = visibleCards[0].getBoundingClientRect().top;
            while (
                cols < visibleCards.length &&
                visibleCards[cols].getBoundingClientRect().top === firstCardTop
            ) {
                cols++;
            }

            function getPosition(card) {
                const index = visibleCards.indexOf(card);
                return {
                    row: Math.floor(index / cols),
                    col: index % cols,
                };
            }

            switch (e.code) {
                case ARROW_LEFT:
                    const leftPos = getPosition(currentCard);
                    if (leftPos.col > 0) {
                        nextCard =
                            visibleCards[
                                leftPos.row * cols + (leftPos.col - 1)
                            ];
                    }
                    break;
                case ARROW_RIGHT:
                    const rightPos = getPosition(currentCard);
                    if (rightPos.col < cols - 1) {
                        nextCard =
                            visibleCards[
                                rightPos.row * cols + (rightPos.col + 1)
                            ];
                    }
                    break;
                case ARROW_UP:
                    const upPos = getPosition(currentCard);
                    if (upPos.row > 0) {
                        nextCard =
                            visibleCards[(upPos.row - 1) * cols + upPos.col];
                    }
                    break;
                case ARROW_DOWN:
                    const downPos = getPosition(currentCard);
                    if (
                        downPos.row <
                        Math.floor(visibleCards.length / cols) -
                            (visibleCards.length % cols > downPos.col ? 0 : 1)
                    ) {
                        nextCard =
                            visibleCards[
                                (downPos.row + 1) * cols + downPos.col
                            ];
                    }
                    break;
            }

            if (nextCard) {
                nextCard.focus();
                e.preventDefault();
            }
        }
    }
}

if (!customElements.get(MERCH_CARD.toLowerCase())) {
    customElements.define('merch-card', MerchCard);
}
